import React, { useState, useRef, useEffect, useCallback } from 'react';
import axios from 'axios';
import Markdown from 'react-markdown';
import './styles.css';


const ArticleGenerator = () => {
    const [inputData, setInputData] = useState({
        focusKeyword: '',
        contentContext: '',
        desiredSentiment: 'Positive',
        powerWordType: 'Greed and FOMO',
        includeNumber: 'Yes',
        articleLength:'Short',
        Language:"English"
    });
  const [sections, setSections] = useState([]);
  const [title, setTitle] = useState('');
  const [output, setOutput] = useState('');
  const [currentStep, setCurrentStep] = useState(1);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [editingSections, setEditingSections] = useState({}); // State to track which sections are being edited
  const [combinedContent, setCombinedContent] = useState(''); // State to hold the combined content of all sections

  const handleInputChange = (e) => {
    setInputData({ ...inputData, [e.target.name]: e.target.value });
  };
      useEffect(() => {
        const combined = Object.values(sections).join('\n\n');
        setCombinedContent(combined);
    }, [sections]);

      const generateSectionContent = async (sectionTitle) => {
        setLoading(true);
        try {
            const response = await axios.post("https://us-central1-foresight-club.cloudfunctions.net/onSection", {
                Title: sectionTitle
            });
            setSections(prevSections => ({
                ...prevSections,
                [sectionTitle]: response.data
            }));
        } catch (error) {
            console.error("Error generating content for section:", error);
            setError("An error occurred while generating content.");
        } finally {
            setLoading(false);
        }
    };
    const regenerateSection = async (sectionTitle) => {
        setLoading(true);
        try {
            const response = await axios.post("API_ENDPOINT", { Title: sectionTitle });
            setSections(prevSections => ({
                ...prevSections,
                [sectionTitle]: response.data
            }));
        } catch (error) {
            console.error("Error regenerating section:", error);
            setError("An error occurred while regenerating the section.");
        } finally {
            setLoading(false);
        }
    };
      // Function to remove Roman numerals from section titles
const removeRomanNumerals = (title) => {
    console.log(title.replace(/^[IVXLCDM]+\.\s*/, ''))
    return title.replace(/^[IVXLCDM]+\.\s*/, '');
};

    const generateAllSections = async () => {
        setLoading(true);
        try {
            const sectionTitles = Object.keys(sections);
            for (const title of sectionTitles) {
                if (!sections[title]) { // Check if content already exists
                    await generateSectionContent(title); // Generate content for this section
                }
            }
        } catch (error) {
            console.error("Error generating content for all sections:", error);
            setError("An error occurred while generating content.");
        } finally {
            setLoading(false);
            setCurrentStep(4);
        }
    };
  // Function to toggle edit mode on and off
  const toggleEditMode = useCallback((sectionTitle) => {
    setEditingSections((prev) => ({
      ...prev,
      [sectionTitle]: !prev[sectionTitle],
    }));
  }, []);

  // Function to delete a section
  const deleteSection = useCallback((sectionTitle) => {
    setSections((prev) => {
      const newSections = { ...prev };
      delete newSections[sectionTitle];
      return newSections;
    });
  }, []);

    // Function to copy content to clipboard
    const copyToClipboard = () => {
        const contentToCopy = `${title}\n\n${combinedContent}`;
        navigator.clipboard.writeText(contentToCopy)
            .then(() => {
                // Handle successful copy
                alert('Content copied to clipboard!');
            })
            .catch(err => {
                // Handle copy error
                console.error('Failed to copy text: ', err);
            });
    };
    
const Section = ({ title, content, isEditing, onEdit, onDelete, onSave }) => (
    <div className="section">
        <Markdown>{title}</Markdown>
        {isEditing ? (
            <>
                <button onClick={onDelete}>Delete</button>
                <button onClick={onSave}>Save</button>
            </>
        ) : (
            <>
                <Markdown className="section-content">{content}</Markdown>
                <button onClick={onEdit}>Edit</button>
                <button onClick={onDelete}>Delete</button>
            </>
        )}
    </div>
);

      const parseContent = (content) => {
        const sectionPattern = /(?:\n|^)([IVXLCDM]+\..+?)(?=\n[IVXLCDM]+\..+|\n*$)/gs;
        const parsedSections = {};
        let match;
    
        while ((match = sectionPattern.exec(content)) !== null) {
            const [fullMatch, sectionTitleWithNumeral] = match;
            const sectionTitle = removeRomanNumerals(sectionTitleWithNumeral.trim()); // Remove Roman numerals here
            parsedSections[sectionTitle] = fullMatch.replace(sectionTitleWithNumeral, '').trim();
        }
    
        setSections(parsedSections);
    };
    const fetchData = async (dataToFetch) => {
        if (!dataToFetch.focusKeyword) {
            setError("Please provide a focus keyword!");
            return;
        }
        setLoading(true);
        console.log(dataToFetch)
        try {
            const response = await axios.post("https://us-central1-foresight-club.cloudfunctions.net/onTitle", {
                ...dataToFetch
            });
            console.log(response)
            setOutput(response.data);
            
            setTitle(response.data);
            setCurrentStep(2);
        } catch (err) {
            console.error("Error fetching data:", err);
            setError("An error occurred while generating the article title.");
        } finally {
            setLoading(false);
        }
    };
     const InputSection = React.memo(({ handleInputChange, fetchData, error, loading }) => {

        const [localInputData, setLocalInputData] = useState(inputData);
    
        const handleLocalInputChange = (e) => {
            const { name, value } = e.target;
            const newData = { ...localInputData, [name]: value };
            setLocalInputData(newData);
        };
        
    const handleGenerateClick = () => {
    console.log("localInputData on Generate:", localInputData);
    fetchData(localInputData);
}

    
        return (
         
            <div className="input-section">
            
                <div>
                    <label>Focus Keyword <span className="required">*</span></label>
                    <input 
                        name="focusKeyword"
                        value={localInputData.focusKeyword}
    onChange={handleLocalInputChange}
    disabled={loading}
                    />
                </div>
                <div>
                    <label>Content Context (Optional)</label>
                    <input 
                        name="contentContext"
                        value={localInputData.contentContext}
                        onChange={handleLocalInputChange}
                        disabled={loading}
                    />
                </div>
                <div>
                    <label>Desired Sentiment</label>
                    <select name="desiredSentiment" value={localInputData.desiredSentiment} onChange={handleLocalInputChange} disabled={loading}>
                        <option value="Positive">Positive</option>
                        <option value="Negative">Negative</option>
                    </select>
                </div>
                <div>
                    <label>Power Word</label>
                    <select name="powerWordType"  value={localInputData.powerWordType} 
    onChange={handleLocalInputChange} 
    disabled={loading}>
                        <option value="Greed and FOMO">Greed and FOMO</option>
                        <option value="Curiosity">Curiosity</option>
                        <option value="Ease and Convenience">Ease and Convenience</option>
                        <option value="Desire and Lust">Desire and Lust</option>
                        <option value="Vanity and Bragging">Vanity and Bragging</option>
                        <option value="Trust">Trust</option>
                        <option value="Anger">Anger</option>
                        <option value="Fear">Fear</option>
                    </select>
                </div>
                <div>
                    <label>Include Number</label>
                    <select name="includeNumber" value={localInputData.includeNumber} onChange={handleLocalInputChange} disabled={loading}>
                        <option value="Yes">Yes</option>
                        <option value="No">No</option>
                    </select>
                       {/* Slider for Article Length */}
            <div>
                <label>Article Length</label>
                <select name="articleLength" value={localInputData.articleLength} onChange={handleLocalInputChange}>
                    <option value="Short">Short</option>
                    <option value="Medium">Medium</option>
                    <option value="Long">Long</option>
                </select>
            </div>
                   {/* Dropdown for Language Selection */}
                   <div>
                <label>Language</label>
                <select name="Language" value={localInputData.language} onChange={handleLocalInputChange}>
                    <option value="English">English</option>
                    <option value="Spanish">Spanish</option>
                    {/* Add more languages as needed */}
                </select>
            </div>

         
                {error && <div className="error-message">{error}</div>}
                <button onClick={handleGenerateClick} disabled={loading}>Generate</button>
            </div>
            </div>
        );
    });
    
  const fetchSections = async () => {
    // Placeholder for fetching sections - integrate with your backend or service
    setLoading(true);
    try {
      setLoading(true);
              console.log(title)
                  const response = await axios.post("https://us-central1-foresight-club.cloudfunctions.net/onArticle", {
                      Title: title
                  });
                  setOutput(response.data);
                  parseContent(response.data)
                  console.log(response)
      setCurrentStep(3);
    } catch (err) {
      setError('Failed to fetch sections.');
    }
    setLoading(false);
  };

  return (
    <div className="ArticleGenerator">
      {currentStep === 1 && (
      <InputSection 
                  inputData={inputData}
                  handleInputChange={handleInputChange}
                  fetchData={fetchData}
                  error={error}
                  loading={loading}
              />
      )}

      {currentStep === 2 && title && (
        <div className="sections-container">
          <h2>{title}</h2>
          <button onClick={fetchSections} disabled={loading}>
            Generate Sections
          </button>
        </div>
      )}

{currentStep === 3 && Object.keys(sections).length > 0 && (
  <div className="final-article-container">
    {Object.entries(sections).map(([sectionTitle, sectionContent]) => {
      const titleWithoutRomanNumeral = removeRomanNumerals(sectionTitle);
      return (
        <Section
          key={sectionTitle}
          title={titleWithoutRomanNumeral}
          content={sectionContent}
         // isEditing={editingSections[sectionTitle]}
          onEdit={() => toggleEditMode(sectionTitle)}
          onDelete={() => deleteSection(sectionTitle)}
          onSave={() => toggleEditMode(sectionTitle)} // Assuming you handle the save logic here
        />
      );
    })}
    <div className="generate-all-button-container">
               <button onClick={generateAllSections} disabled={loading}>
                    Generate All Sections
                 </button>
            </div>
    
  </div>
)}
       {currentStep === 4 && (
                <div className="combined-content">
                    <Markdown className="article-content">{combinedContent}</Markdown>
                    <button onClick={copyToClipboard} disabled={!combinedContent}>
                        Copy Article
                    </button>
                </div>
            )}

      {loading && <div className="loading">Loading...</div>}
      {error && <div className="error">{error}</div>}
    </div>
  );
};

export default ArticleGenerator;